.App{
    height: 100vh;
    padding: 0px 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

}

.books{
    display: flex;
    gap: 10px;
}
.book{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap:10px;
    align-items: center;
}
img{
    width: 200px;
    height: 300px;
    object-fit: cover;
    background-color: aquamarine;
}
.delete{
    border: none;
    padding: 3px 6px;
    background-color: white;
    cursor: pointer;
    border:1px solid pink;
    color:palevioletred;
}
.update{
    border: none;
    padding: 3px 6px;
    background-color: white;
    cursor: pointer;
    border:1px solid skyblue;
    color:blue;
}
.form{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
input{
    width: 250px;
    padding: 10px;
    border: 1px solid gray;
}
.formButton{
    border: none;
    padding: 10px;
    background-color: orange;
    color: white;
    font-weight: bold;
    cursor: pointer;
}